import { request } from 'utils';

const prefix = '/api/education';

export const browseClassNoGraduated = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true
}) =>
  request({
    prefix,
    url: '/Schedule/BrowseClassNoGraduated',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const browseScheduleByClass = ({
  classId,
  yearEduId,
  semesterId,
  week = 1,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true
}) =>
  request({
    prefix,
    url: '/Schedule/BrowseScheduleByClass',
    method: 'GET',
    params: {
      classId,
      yearEduId,
      semesterId,
      week,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {},
  });

export const createListScheduleWithDetail = (schedulesData) =>
  request({
    prefix,
    url: `/Schedule/CreateListScheduleWithDetail`,
    data: {
      schedules: schedulesData
    },
    method: 'POST',
  });

export const duplicateSchedule = ({
  classId,
  yearEduId,
  semesterId,
  fromWeek,
  toWeek,
}) =>
  request({
    prefix,
    url: '/Schedule/DuplicateSchedule',
    method: 'POST',
    data: {
      classId,
      yearEduId,
      semesterId,
      fromWeek,
      toWeek,
    },
  });

export const autoPlanSubjectDetails = (data) =>
  request({
    prefix,
    url: `/Schedule/CreateScheduleAuto`,
    data,
    method: 'POST',
  });



