import React, { Component } from 'react';
import ReactDataSheet from 'react-datasheet';
// Be sure to include styles at some point, probably during your bootstrapping
import 'react-datasheet/lib/react-datasheet.css';
import { Col, Row, message } from 'antd';
import { dataClasses } from 'dump/data';
import './styles.scss';

export default class DataSheet extends Component {
  constructor(props) {
    super(props);
    this.selectedCells = {};
    this.state = {
      lessonTime: [
        {
          ca: 'SÃ¡ng',
          lession: 1,
        },
        {
          lession: 2,
        },
        {
          lession: 3,
        },
        {
          lession: 4,
        },
        {
          lession: 5,
        },
        {
          ca: 'Chiá»u',
          lession: 6,
        },
        {
          lession: 7,
        },
        {
          lession: 8,
        },
        {
          lession: 9,
        },
        {
          lession: 10,
        },
      ],
      grid: (() => {
        const dataDemo = [];
        for (let i = 0; i < 10; i += 1) {
          const item = [];
          for (let j = 0; j < 6; j += 1) {
            let rowspan = 1;
            if (i === 0 && j === 1) {
              rowspan = 4;
            }
            item.push({ value: '', code: 10 * i + j, rowspan });
          }
          dataDemo.push(item);
        }
        return dataDemo;
      })(),
    };
  }


  setSelectedCellValue = (startRowIndex, endRowIndex, columnIndex, subjectLabel, key) => {
    const { grid } = this.state;
    const tempGrid = [...grid];
    for (let index = startRowIndex; index <= endRowIndex; index += 1) {
      const rowData = tempGrid[index];
      rowData[columnIndex].value = subjectLabel;
      rowData[columnIndex].key = key;
    }
    this.setState({ grid: [...tempGrid] });
    this.selectedCells = {};
  }

  handleDeletePlan = () => {
    if (!this.selectedCells || !this.selectedCells.start) {
      message.warning('Vui lÃ²ng chá»n thá»i gian');
      return;
    }
    const { grid } = this.state;
    const startRowIndex = this.selectedCells.start.i;
    const endRowIndex = this.selectedCells.end.i;
    const startColIndex = this.selectedCells.start.j;
    const endColIndex = this.selectedCells.end.j;
    const tempGrid = [...grid];
    const listKeyInSelectedCell = [];
    // get list key
    for (let indexI = startRowIndex; indexI <= endRowIndex; indexI += 1) {
      for (let indexJ = startColIndex; indexJ <= endColIndex; indexJ += 1) {
        const colData = tempGrid[indexI][indexJ];
        listKeyInSelectedCell.push(colData.key);
      }
    }
    // delete item base on list key
    tempGrid.forEach(rowData => {
      rowData.forEach(colData => {
        if (listKeyInSelectedCell.includes(colData.key)) {
          colData.value = '';
          delete colData.key;
        }
      });
    });
    this.props.handleRemoveSelectedSelectedCells(listKeyInSelectedCell);
    this.setState({ grid: [...tempGrid] });
    this.selectedCells = {};
  };

  isExistSubject = (startRowIndex, endRowIndex, columnIndex) => {
    const { grid } = this.state;
    const tempGrid = [...grid];
    let isExistSubject = false;
    for (let index = startRowIndex; index <= endRowIndex; index += 1) {
      const rowData = tempGrid[index];
      if (rowData[columnIndex].key) {
        isExistSubject = true;
        break;
      }
    }
    return isExistSubject;
  }
  
  resetCellsSheet = () => {
    const dataDemo = [];
    for (let i = 0; i < 10; i += 1) {
      const item = [];
      for (let j = 0; j < 6; j += 1) {
        let rowspan = 1;
        if (i === 0 && j === 1) {
          rowspan = 4;
        }
        item.push({ value: '', code: 10 * i + j, rowspan });
      }
      dataDemo.push(item);
    }
    this.setState({
      grid: [...dataDemo],
    });
  }

  render() {
    return (
      <Row>
        <Col span={24}>
          <ReactDataSheet
            style={{ width: '100%' }}
            data={this.state.grid}
            onSelect={({ start, end }) => {
              this.selectedCells = {
                start: start.i > end.i ? end : start,
                end: start.i > end.i ? start : end,
              };
              this.props.handleSetSelectedSelectedCells({ ...this.selectedCells });
            }}
            sheetRenderer={props => (
              <table
                style={{ width: '100%' }}
                className={`${props.className} my-awesome-extra-class`}
              >
                <thead>
                  <tr>
                    <th>Ca há»c</th>
                    <th>Tiáº¿t</th>
                    <th>Thá»© 2</th>
                    <th>Thá»© 3</th>
                    <th>Thá»© 4</th>
                    <th>Thá»© 5</th>
                    <th>Thá»© 6</th>
                    <th>Thá»© 7</th>
                  </tr>
                </thead>
                <tbody>{props.children}</tbody>
              </table>
            )}
            rowRenderer={props => (
              <tr>
                {this.state.lessonTime[props.row].ca &&
                  <td
                    rowSpan={5}
                    style={{
                      textAlign: 'center',
                      verticalAlign: 'middle',
                    }}
                  >
                    {this.state.lessonTime[props.row].ca}
                  </td>
                }
                <td>{this.state.lessonTime[props.row].lession}</td>
                {props.children}
              </tr>
            )}
            valueRenderer={cell => (
              <div
                style={{
                  background: !dataClasses[cell.value] ? '#fff' : dataClasses[cell.value].color,
                  height: 27,
                  textAlign: 'center',
                  verticalAlign: 'middle',
                  lineHeight: '27px',
                }}
              >
                {cell.value}
              </div>
            )}
            attributesRenderer={cell => ({ 'data-key': cell.key } || {})}
          />
        </Col>
      </Row>
    );
  }
}



