import React, { Component } from 'react';
import { Col, Form, Row } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getStaffById } from 'services/teacher';
import { getListSubject } from 'services/Subject';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };
  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lÃ²ng chá»n Giáº£ng ViÃªn!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lÃ²ng chá»n MÃ´n Há»c!',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col xs={24} sm={8} md={8}>
            <Form.Item label="NÄƒm há»c">
              {getFieldDecorator('yearEduId', optionFieldDecorator.yearEduId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getStaffById}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={8} md={8}>
            <Form.Item label="Há»c kÃ¬">
              {getFieldDecorator('semesterId', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getListSubject}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={8} md={8}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



