import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin, message } from 'antd';

import { browseScheduleByClass, createListScheduleWithDetail, autoPlanSubjectDetails } from 'services/schedule';
import { browsePlanSubjectDetailByClass, browsePlanSubjectDetailSplitByClass } from 'services/planSubjectDetailâ€‹';

import { getQueryParams } from 'utils/urlUtil';
import { CartHeadAction } from 'components/common';
import { makeid } from 'utils/functionUtil';

import FormSearch from './FormSearch';
import TablePlanSubjectDetail from './TablePlanSubjectDetail';
import TablePlanSubjectDetailSplit from './TablePlanSubjectDetailSplit';
import DataSheet from './DataSheet';
import MenuTabTop from './MenuTabTop';
import ModalDuplicatePlan from './ModalDuplicatePlan';

class ManualSchedulePage extends Component {
  constructor(props) {
    super(props);
    this.refDataSheet = React.createRef();
    this.selectedCells = {};
    this.planDetailDataToSave = {};
    this.planDetailDataToDelete = [];
    this.state = {
      loading: false,
      dataPlanSubjectDetailByClass: null,
      dataPlanSubjectDetailByClassSplit: null,
      dataPlanSubjectDetailByClassSplitToShow: null,
      // selectedRows: null,
      rowKey: "uniqueId",
      openModalDup: false,
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      this.refDataSheet.current.resetCellsSheet();
      this.setState({
        loading: true,
        dataPlanSubjectDetailByClass: null,
        dataPlanSubjectDetailByClassSplitToShow: null,
        dataPlanSubjectDetailByClassSplit: null,
      });
      this.planDetailDataToSave = {};
      this.planDetailDataToDelete = [];
      const paramSearch = getQueryParams();
      if (paramSearch.classId && paramSearch.yearEduId && paramSearch.semesterId) {
        await Promise.all([
          browseScheduleByClass(paramSearch),
          browsePlanSubjectDetailByClass(paramSearch),
          browsePlanSubjectDetailSplitByClass(paramSearch),
        ]).then(values => {
          let tempPlanSubjectData = [...values[1].data.items];
          tempPlanSubjectData = tempPlanSubjectData.map(item => {
            item.uniqueId = `${makeid(8)}_${item.id}`;
            return item;
          })
          let tempPlanSubjectSplitData = [...values[2].data.items];
          const tempPlanSubjectSplitDataToShow = [];
          tempPlanSubjectSplitData = tempPlanSubjectSplitData.map(item => {
            if (!item.uniqueId) {
              item.uniqueId = `${makeid(8)}_${item.id}`;
              tempPlanSubjectSplitDataToShow.push({ ...item });
            }
            return item;
          })
          let tempScheduleData = [...values[0].data.items];
          if (tempScheduleData.length) {
            tempScheduleData = tempScheduleData.forEach(item => {
              const decodeUniqueId = item.uniqueId.split('_');
              const startRowIndex = decodeUniqueId[1] - 1;
              const endRowIndex = decodeUniqueId[2] - 1;
              const columnIndex = decodeUniqueId[3] - 1;
              const { subjectLabel } = item;
              const keyOfDataToSave = item.uniqueId;

              this.planDetailDataToSave[keyOfDataToSave] = {
                fromStudyPeriodId: item.fromStudyPeriodId,
                toStudyPeriodId: item.toStudyPeriodId,
                psdId: item.psdId,
                dayOfWeek: item.dayOfWeek,
                isDelete: item.isDelete,
              };
              this.refDataSheet.current.setSelectedCellValue(startRowIndex, endRowIndex, columnIndex, subjectLabel, keyOfDataToSave);
            })
          }
          this.setState({
            loading: false,
            dataPlanSubjectDetailByClass: [...tempPlanSubjectData],
            dataPlanSubjectDetailByClassSplitToShow: [...tempPlanSubjectSplitDataToShow],
            dataPlanSubjectDetailByClassSplit: [...tempPlanSubjectSplitData],
          });
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSelectedPlanSubject = (planSubject) => {
    try {
      if (!this.selectedCells || !this.selectedCells.start) {
        message.warning('Vui lÃ²ng chá»n thá»i gian');
        return;
      }
      if (this.selectedCells.start.j !== this.selectedCells.end.j) {
        message.warning('Vui lÃ²ng chá»n thá»i gian trong cÃ¹ng 1 ngÃ y');
        return;
      }
      const numSelectedCells = this.selectedCells.end.i - this.selectedCells.start.i + 1;
      if (planSubject.periodInSession !== numSelectedCells) {
        message.warning('Sá»‘ tiáº¿t Ä‘Æ°á»£c chá»n khÃ´ng khá»›p vá»›i MÃ´n há»c!');
        return;
      }
      const startRowIndex = this.selectedCells.start.i;
      const endRowIndex = this.selectedCells.end.i;
      const columnIndex = this.selectedCells.start.j;
      const { subjectLabel } = planSubject;
      const keyOfDataToSave = planSubject.uniqueId;
      if (this.refDataSheet.current.isExistSubject(startRowIndex, endRowIndex, columnIndex)) {
        message.warning('Vui lÃ²ng xÃ³a MÃ´n há»c cÅ© trÆ°á»›c khi chá»n MÃ´n há»c má»›i');
        return;
      }
      if ((startRowIndex >= 5 || endRowIndex >= 5) && planSubject.studySessionId === 1) {
        message.warning('MÃ´n há»c chá»‰ Ä‘Æ°á»£c chá»n cho tiáº¿t Buá»•i SÃ¡ng');
        return;
      }
      if ((startRowIndex <= 4 || endRowIndex <= 4) && planSubject.studySessionId === 2) {
        message.warning('MÃ´n há»c chá»‰ Ä‘Æ°á»£c chá»n cho tiáº¿t Buá»•i Chiá»u');
        return;
      }
      let numOfPlanSubjectSet = 0;
      Object.keys(this.planDetailDataToSave).forEach(key => {
        const idOfPlanSubject = key;
        if (idOfPlanSubject === planSubject.uniqueId) {
          numOfPlanSubjectSet += 1;
        }
      })
      if (numOfPlanSubjectSet >= 1) {
        message.warning('Sá»‘ tiáº¿t cho MÃ´n há»c nÃ y Ä‘Ã£ Ä‘á»§!');
        return;
      }

      // remove item show on DetailSPlit
      const { dataPlanSubjectDetailByClassSplitToShow } = this.state;
      const tempPlanSubjectSplitDataToShow = [...dataPlanSubjectDetailByClassSplitToShow].filter(item => item.uniqueId !== keyOfDataToSave);
      this.setState({
        dataPlanSubjectDetailByClassSplitToShow: [...tempPlanSubjectSplitDataToShow],
      });

      // add item to list will save
      this.planDetailDataToSave[keyOfDataToSave] = {
        fromStudyPeriodId: startRowIndex + 1,
        toStudyPeriodId: endRowIndex + 1,
        psdId: planSubject.id,
        dayOfWeek: columnIndex + 1
      };

      // add to Excel View
      this.refDataSheet.current.setSelectedCellValue(startRowIndex, endRowIndex, columnIndex, subjectLabel, keyOfDataToSave);
    } catch (error) {
      console.log(error);
    } finally {
      this.selectedCells = {};
    }
  }

  handleSetSelectedSelectedCells = (selectedCell) => {
    this.selectedCells = { ...selectedCell };
  }

  handleRemoveSelectedSelectedCells = (listKeyInSelectedCell) => {
    try {
      const {
        dataPlanSubjectDetailByClassSplit,
        dataPlanSubjectDetailByClassSplitToShow,
      } = this.state;
      const tempPlanSubjectSplitDataToShow = [...dataPlanSubjectDetailByClassSplitToShow];
      listKeyInSelectedCell.forEach(uniqueId => {
        // delte item in list to save
        if (this.planDetailDataToSave && this.planDetailDataToSave[uniqueId]) {
          const itemNeedDelete = { ...this.planDetailDataToSave[uniqueId] };
          if (Object.prototype.hasOwnProperty.call(itemNeedDelete, "isDelete")) {
            itemNeedDelete.isDelete = true;
            this.planDetailDataToDelete.push({ ...itemNeedDelete });
          }
          delete this.planDetailDataToSave[uniqueId];
          // add Subject again to DetailSplit table
          const restoreItem = dataPlanSubjectDetailByClassSplit.find(item => item.uniqueId === uniqueId);
          tempPlanSubjectSplitDataToShow.push(restoreItem);
        }
      });
      this.setState({
        dataPlanSubjectDetailByClassSplitToShow: [...tempPlanSubjectSplitDataToShow],
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.selectedCells = {};
    }
  }

  savePlanSubject = async () => {
    const listPlan = [];
    Object.keys(this.planDetailDataToSave).forEach(uniqueId => {
      listPlan.push(this.planDetailDataToSave[uniqueId]);
    })
    if (!listPlan.length) {
      message.warning('Vui lÃ²ng chá»n MÃ´n há»c cho Tiáº¿t!');
      return;
    }
    try {
      this.setState({
        loading: true,
      });
      await createListScheduleWithDetail([...listPlan, ...this.planDetailDataToDelete]);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  autoPlanSubject = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      if (paramSearch.classId && paramSearch.yearEduId && paramSearch.semesterId) { 
        await autoPlanSubjectDetails(paramSearch);
        this.handleChangeQueryParams();
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  deleteCellsSheetSelected = () => {
    this.refDataSheet.current.handleDeletePlan();
    this.selectedCells = {};
  }

  openDuplicatePlanSubject = async () => {
    this.setState({
      openModalDup: true,
    });
  }

  closeModal = () => {
    this.setState({
      openModalDup: false,
    });
  }

  handleCancelPopup = () => {
    this.setState({
      openModalDup: false,
    });
  }

  render() {
    const {
      dataPlanSubjectDetailByClass,
      dataPlanSubjectDetailByClassSplitToShow,
      rowKey,
      loading,
      openModalDup,
    } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title="Xáº¿p TKB thá»§ cÃ´ng">
          <Row gutter={24}>
            <Col span={24}>
              <FormSearch />
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <TablePlanSubjectDetailSplit
                data={dataPlanSubjectDetailByClassSplitToShow}
                handleSelectedPlanSubject={this.handleSelectedPlanSubject}
                rowKey={rowKey}
              />
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <CartHeadAction
                style={{
                  textAlign: 'right',
                  margin: '0px -25px 10px -25px',
                  borderBottom: 0,
                }}
              >
                <MenuTabTop
                  // onExportExcel={this.handleExportExcel}
                  // onExportPDF={this.handleExportPDF}
                  savePlanSubject={this.savePlanSubject}
                  autoPlanSubject={this.autoPlanSubject}
                  openDuplicatePlanSubject={this.openDuplicatePlanSubject}
                  deleteCellsSheetSelected={this.deleteCellsSheetSelected}
                />
              </CartHeadAction>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              {' '}
              <DataSheet
                ref={this.refDataSheet}
                handleSetSelectedSelectedCells={this.handleSetSelectedSelectedCells}
                handleRemoveSelectedSelectedCells={this.handleRemoveSelectedSelectedCells}
              />
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <TablePlanSubjectDetail
                data={dataPlanSubjectDetailByClass}
                rowKey={rowKey}
              />
            </Col>
          </Row>
        </Card>
        {openModalDup &&
          <ModalDuplicatePlan
            visible={openModalDup}
            closeModal={this.closeModal}
          />
        }
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManualSchedulePage />
  </Page>
);



